@import 'variables.scss';

body {
    margin: 0px;
}

.haku-spreadsheet {
    position: relative;
    width: 100vw;
    height: 100vh;
    display: none;
    opacity: 0.0;

    &.init {
        display: block;
        transition: opacity 0.3s;
    }

    &.animeinit {
        opacity: 1.0;
    }

    //左侧头部
    > .header-body-left {
        position: absolute;
        top: 20px;
        width: 50px;
        height: calc(100% - 20px);
        overflow: hidden;
        background-color: #EEE;

        > ul {
            text-align: center;
            width: 0px;
            padding-left: 0px;
            margin: 0px;

            > li {
                user-select: none;
                -moz-user-select: none;
                position: relative;
                list-style: none;
                color: #666;
                font-size: 12px;
                width: 48px;
                line-height: 24px;
                padding: 0px;
                border-bottom: 1px solid #DDD;
                border-right: 2px solid #EEE;
                cursor: default;

                &:after {
                    content: '';
                    position: absolute;
                    cursor: url('data:application/octet-stream;base64,'),auto;
                    top: 3px;
                    left: 0px;
                    width: calc(100% + 2px);
                    height: calc(100% - 6px);
                }

                &:hover {
                    background-color: #b7d2ff;
                    border-right: 2px solid #b7d2ff;
                }

                &.active {
                    border-right: 2px solid #448AFF;
                    background-color: #b7d2ff;
                }

                &:first-child {
                    border-top: 1px solid #DDD;
                }

                > .header-split {
                    position: absolute;
                    cursor: n-resize;
                    bottom: -5px;
                    left: 0px;
                    width: calc(100% + 2px);
                    height: 10px;
                    z-index: 2;
                }
            }
        }
    }

    //顶部头部
    > .header-body-top {
        position: absolute;
        top: 0px;
        left: 50px;
        width: calc(100% - 50px);
        height: 20px;
        overflow: hidden;
        background-color: #EEE;

        > ul {
            width: 100%;
            padding-left: 0px;
            margin: 0px;
            white-space: nowrap;

            > li {
                user-select: none;
                -moz-user-select: none;
                position: relative;
                display: inline-block;
                text-align: center;
                list-style: none;
                color: #666;
                font-size: 12px;
                width: 99px;
                line-height: 20px;
                height: 18px;
                padding: 0px;
                border-right: 1px solid #DDD;
                border-bottom: 2px solid #EEE;
                vertical-align: top;
                cursor: default;

                &:after {
                    content: '';
                    position: absolute;
                    cursor: url('data:application/octet-stream;base64,'),auto;
                    top: 0px;
                    left: 0px;
                    width: 100%;
                    height: calc(100% + 2px);
                }

                &:hover {
                    background-color: #b7d2ff;
                    border-bottom: 2px solid #b7d2ff;
                }

                &.active {
                    border-bottom: 2px solid #448AFF;
                    background-color: #b7d2ff;
                }

                &:first-child {
                    border-left: 1px solid #DDD;
                }

                > .header-split {
                    position: absolute;
                    cursor: e-resize;
                    top: 0px;
                    left: -5px;
                    width: 10px;
                    height: calc(100% + 2px);
                    z-index: 2;
                }
            }
        }
    }

    > .haku-spreadsheet-main {
        position: relative;
        height: calc(100% - 20px);
        width: calc(100% - 50px);
        overflow: hidden; //overflow: scroll;
        top: 20px;
        left: 50px;

        > .haku-spreadsheet-main-body {
            position: relative;
            display: block;
        }
    }
}

//内容表格
.haku-table {
    border-spacing: 0px;

    > tbody {

        > tr {
            white-space: nowrap;

            > td {
                position: relative;
                box-sizing: border-box;
                padding: 0px;
                border-top: 1px solid $--color-table-border;
                border-left: 1px solid $--color-table-border;
                border-bottom: 1px solid transparent;
                border-right: 1px solid transparent;
                background-color: white;
                user-select: none;
                -moz-user-select: none;
                cursor: cell;

                &.cell-freeze-left {
                    border-left: 1px solid #888;
                }
                &.cell-freeze-top {
                    border-top: 1px solid #888;
                }

                &.hidden {
                    display: none;
                }

                &.selected {
                    background-color: #F3F3F3;
                    color: #333;
                }

                &:after {
                    position: absolute;
                    content: '';
                    opacity: 0;
                    bottom: -5px;
                    right: -5px;
                    width: 6px;
                    height: 6px;
                    z-index: 1;
                    border-radius: 1px;
                    background-color: #67b0ec;
                    border: 1px solid white;
                }


                //拖拽框：左
                &.drag-area-left {
                    border-left-color: #4CAF50;
                    border-left-style: dashed;
                    // padding-left: 0px;

                    &.drag-area-top {
                        border-top-left-radius: 4px;
                    }

                    &.drag-area-bottom {
                        border-bottom-left-radius: 4px;
                    }
                }
                //拖拽框：右
                &.drag-area-right {
                    border-right-color: #4CAF50;
                    border-right-style: dashed;

                    &.drag-area-top {
                        border-top-right-radius: 4px;
                    }

                    &.drag-area-bottom {
                        border-bottom-right-radius: 4px;
                    }
                }
                //拖拽框：上
                &.drag-area-top {
                    border-top-color: #4CAF50;
                    border-top-style: dashed;
                }
                //拖拽框：下
                &.drag-area-bottom {
                    border-bottom-color: #4CAF50;
                    border-bottom-style: dashed;
                }


                //选框：左
                &.selected-area-left {
                    border-left-color: $--color-main;
                    // padding-left: 0px;
                    box-shadow: 2px 0px 0 0px white inset;

                    &.selected-area-right {
                        box-shadow: -2px 0px 0px 0px white inset, 2px 0px 0px 0px white inset, 0px 0px 0px 0px white inset;

                        &.selected-area-bottom {
                            box-shadow: -2px 0px 0px 0px white inset, 2px 0px 0px 0px white inset, 0px -2px 0px 0px white inset, 0px 0px 0px 0px white inset;
                        }
                    }

                    &.selected-area-top {
                        border-top-left-radius: 4px;
                        box-shadow: 2px 2px 0 0 white inset;
                    }

                    &.selected-area-bottom {
                        border-bottom-left-radius: 4px;
                        box-shadow: 1px -1px 0px 1px white inset;
                    }
                }
                //选框：右
                &.selected-area-right {
                    border-right-color: $--color-main;
                    // padding-right: 0px;
                    box-shadow: -2px 0px 0 0px white inset;

                    &.selected-area-top {
                        border-top-right-radius: 4px;
                        box-shadow: -1px 1px 0px 1px white inset;
                    }

                    &.selected-area-bottom {
                        border-bottom-right-radius: 4px;
                        box-shadow: -1px -1px 0px 1px white inset;

                        &:after {
                            opacity: 1;
                        }
                    }
                }
                //选框：上
                &.selected-area-top {
                    border-top-color: $--color-main;
                    // padding-top: 0px;
                    box-shadow: 0px 2px 0 0px white inset;
                }
                //选框：下
                &.selected-area-bottom {
                    border-bottom-color: $--color-main;
                    // padding-bottom: 0px;
                    box-shadow: 0px -2px 0 0px white inset;
                }

                //选框：原始位置
                &.selected-area-init {
                    background-color: white;
                }

                // &:focus-within,
                &.focus {
                    background-color: #f0f7ff;
                    z-index: 1;
                    border-radius: 4px;
                    border: none;
                    padding-top: 1px;
                    padding-left: 1px;
                    cursor: text;

                    &.selected-area-top.selected-area-left.selected-area-right.selected-area-bottom {
                        box-shadow: 0 0 3px 1px rgba(16, 119, 255, 0.5);
                    }

                    > .data-txt {

                        &.data-txt-input {
                            opacity: 1.0;
                            user-select: text;
                            -moz-user-select: text;
                        }

                    }
                }

                > .data-txt {
                    position: absolute;
                    user-select: none;
                    -moz-user-select: none;
                    left: 0px;
                    top: 0px;
                    width: 100%;
                    height: 100%;
                    background-color: transparent;
                    display: inline-block;
                    padding: 1px 2px;
                    margin: 0px;
                    box-sizing: border-box;
                    border: none;
                    font-size: 12px;
                    vertical-align: top;
                    line-height: 21px;
                    outline: none;
                    font-family: 'Microsoft YaHei';
                    z-index: 2;

                    &.data-txt-input {
                        resize: none;
                        opacity: 0.0;
                        line-height: 22px;
                        padding-left: 3px;
                        transition: opacity 0.1s;
                    }

                    &:focus {
                        border-radius: 4px;

                    }
                }

                &:last-child {
                    border-right: 1px solid $--color-table-border;
                }

                // &:before {
                //     position: absolute;
                //     content: '';
                //     top: 0px;
                //     left: 0px;
                //     width: 100%;
                //     height: 100%;
                //     z-index: -1;
                // }

            }

            &:last-child {

                > td {
                    border-bottom: 1px solid $--color-table-border;
                }
            }
        }
    }
}

.scroll {
    user-select: none;
    cursor: default;
    position: absolute;
    background-color: rgba(100,100,100,0.2);
    box-shadow: 0px 0px 1px 1px rgba(0,0,0,0.2) inset;
    z-index: 4;
    border-radius: 8px;
    transition: background-color 0.2s, opacity 0.2s;
    // opacity: 0;

    // &:before {
    //     content: '';
    //     position: absolute;
    //     top: 0px;
    //     left: -20px;
    //     height: 100%;
    //     width: 20px;
    //     background-color: transparent;
    //     z-index: -1;
    // }

    &:hover {
        background-color: rgba(100,100,100,0.4);
        // opacity: 1;
    }

    > .scroll-bar {
        position: absolute;
        background: #EEE;
        background: linear-gradient(to bottom, #F4F4F4, #EAEAEA);
        border-radius: 5px;
        box-shadow: 0px 2px 1px 1px rgba(0,0,0,0.15), 0px 0px 1px 1px rgba(255,255,255,0.8) inset;
        transition: background 0.2s;

        &:active {
            background: #DDD;
            background: linear-gradient(to bottom, #E4E4E4, #DADADA);
            box-shadow: 0px 2px 2px 1px rgba(0,0,0,0.25), 0px 0px 1px 1px rgba(255,255,255,0.6) inset;

            &:before {
                border-bottom-color: #AAA;
            }
            &:after {
                border-top-color: #AAA;
            }
        }

        &:before {
            content: '';
            position: absolute;
        }

        &:after {
            content: '';
            position: absolute;
        }
    }

    &.scroll-vertical {
        top: 30px;
        right: 5px;
        width: 14px;
        height: calc(100% - 50px);

        > .scroll-bar {
            top: 2px;
            left: 2px;
            height: 100px;
            width: 10px;

            &:before {
                top: -2px;
                left: 2px;
                width: 0px;
                height: 0px;
                border-top: 5px solid transparent;
                border-bottom: 5px solid #BBB;
                border-left: 3px solid transparent;
                border-right: 3px solid transparent;
            }

            &:after {
                bottom: -2px;
                left: 2px;
                width: 0px;
                height: 0px;
                border-top: 5px solid #BBB;
                border-bottom: 5px solid transparent;
                border-left: 3px solid transparent;
                border-right: 3px solid transparent;
            }
        }
    }

    &.scroll-horizontal {
        left: 60px;
        bottom: 5px;
        height: 14px;
        width: calc(100% - 80px);

        > .scroll-bar {
            left: 2px;
            top: 2px;
            width: 100px;
            height: 10px;

            &:before {
                top: 2px;
                left: -2px;
                width: 0px;
                height: 0px;
                border-top: 3px solid transparent;
                border-bottom: 3px solid transparent;
                border-left: 5px solid transparent;
                border-right: 5px solid #BBB;
            }

            &:after {
                right: -2px;
                top: 2px;
                width: 0px;
                height: 0px;
                border-top: 3px solid transparent;
                border-bottom: 3px solid transparent;
                border-left: 5px solid #BBB;
                border-right: 5px solid transparent;
            }
        }
    }
}

.cursor-grab {
    cursor: -webkit-grab !important;
    cursor: -moz-grab !important;
}

.dragarea {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 0px;
    height: 0px;
    border: 2px dashed #4CAF50;
    box-sizing: border-box;
    background-color: rgba(76, 175, 80, 0.12);
    border-radius: 4px;
    z-index: 1;
}

